const refs = {
  galleryListEl: document.querySelector(".gallery"),
}
console.dir(refs.galleryListEl);

const createGalleryItemEl = (image) => {
  return `
     <li class="gallery-item">
      <a class="gallery-link" href=${image.original}>
        <img
          class="gallery-image"
          src="${image.preview}"
          alt="${image.description}"
         />
      </a>
     </li>
     `;
}

const galleryArr = images.map(el => createGalleryItemEl(el)).join('');

refs.galleryListEl.innerHTML = galleryArr;
console.dir(refs.galleryListEl);

console.log(SimpleLightbox);

// Initialize the instance of Lightbox class
const lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionDelay: 250,
});
